.tooltip {
    display: inline-block;
    position: relative;
    text-align: center;
}

.tooltip .top {
    min-width: 150px;
    top: -12px;
    left: 50%;
    transform: translate(-50%, -100%);
    padding: 5px;
    color: var(--white);
    background-color: var(--black);
    font-weight: normal;
    font-size: 10px;
    border-radius: 8px;
    position: absolute;
    z-index: 1;
    box-sizing: border-box;
    border: 1px solid transparent;
    box-shadow: 0 0 2px var(--grey);
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.8s;
}

.tooltip:hover .top {
    visibility: visible;
    opacity: 1;
}

.tooltip .top i {
    position: absolute;
    top: 99%;
    left: 50%;
    margin-left: -12px;
    width: 24px;
    height: 12px;
    overflow: hidden;
}

.tooltip .top i::after {
    content: '';
    position: absolute;
    width: 12px;
    height: 12px;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
    background-color: var(--black);
    border: 1px solid transparent;
    box-shadow: 0 0 2px var(--grey);
}

